import React from 'react';
import { Card, CardBody, CardHeader, Media, Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { Fade, Stagger } from 'react-animation-components';

function RenderLeader({ leader }) {
  return (
    <div key={leader.id} className="col-12 mt-3">
      <Media li>
        <Media left middle>
          <Media object src={baseUrl + leader.image} alt={leader.name} />
        </Media>
        <Media body className="col-12 ml-2">
          <Media heading>{leader.name}</Media>
          <p>{leader.designation}</p>
          <p>{leader.description}</p>
        </Media>
      </Media>
    </div>
  );
}

function About(props) {

  const leaders = (() => {
    if (props.leaders.isLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
          </div>
        </div>
      );
    } else if (props.leaders.errMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{props.errMess}</h4>
          </div>
        </div>
      );
    } else {
      return (
        <Stagger in>
          {props.leaders.leaders.map((leader) => {
            return (
              <Fade in>
                <Media list>
                  <RenderLeader leader={leader} />
                </Media>
              </Fade>
            );
          })}
        </Stagger>
      );
    }
  })();


  return (
    <div className="container">
      <div className="row">
        <Breadcrumb>
          <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
          <BreadcrumbItem active>About Us</BreadcrumbItem>
        </Breadcrumb>
        <div className="col-12">
          <h3>About Us</h3>
          <hr />
        </div>
      </div>
      <div className="row my-5">
        <div className="col-12 col-md-6">
          <h2>Our History</h2>
          <p>Started in 2010, Ristorante con Fusion quickly established itself as a culinary icon par excellence in
            Hong Kong. With its unique brand of world fusion cuisine that can be found nowhere else, it enjoys
            patronage from the A-list clientele in Hong Kong.  Featuring four of the best three-star Michelin chefs
            in the world, you never know what will arrive on your plate the next time you visit us.</p>
          <p>The restaurant traces its humble beginnings to <em>The Frying Pan</em>, a successful chain started by
            our CEO, Mr. Peter Pan, that featured for the first time the world's best cuisines in a pan.</p>
        </div>
        <div className="col-12 col-md-5">
          <Card>
            <CardHeader className="bg-primary text-white">
              <h4>Facts At a Glance</h4>
            </CardHeader>
            <CardBody>
              <dl className="row">
                <dt className="col-6">Started</dt>
                <dd className="col-6">3 Feb. 2013</dd>
                <dt className="col-6">Major Stake Holder</dt>
                <dd className="col-6">HK Fine Foods Inc.</dd>
                <dt className="col-6">Last Year's Turnover</dt>
                <dd className="col-6">$1,250,375</dd>
                <dt className="col-6">Employees</dt>
                <dd className="col-6">40</dd>
              </dl>
            </CardBody>
          </Card>
        </div>
        <div className="col-12">
          <Card>
            <CardBody className="bg-light">
              <blockquote className="blockquote">
                <p className="mb-0">You better cut the pizza in four pieces because
                  I'm not hungry enough to eat six.</p>
                <footer className="blockquote-footer">Yogi Berra,
                  <cite title="Source Title">The Wit and Wisdom of Yogi Berra,
                    P. Pepe, Diversion Books, 2014</cite>
                </footer>
              </blockquote>
            </CardBody>
          </Card>
        </div>
      </div>
      <hr />
      <div className="row my-5">
        <div className="col-12">
          <h2>Corporate Leadership</h2>
        </div>
        <div className="col-12">

          {leaders}
        </div>
      </div>
    </div>
  );

}

export default About;


